/* JobSection.css */
.job-section {
    padding: 20px;
    background-color: #121212;
    color: #f0f0f0;
  }
  
  .job-search {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
    justify-content: center;
    margin-bottom: 50px;
  }
  .job-search input{
    width: 35%;
  }
  .job-search input, .job-search select, .job-search button {
    padding: 10px;
    border-radius: 4px;
    border: none;
  }
  
  .job-search button {
    background-color: #6a1b9a;
    color: #fff;
    cursor: pointer;
    padding-left: 30px;
    padding-right: 30px;
  }
  
  .filters {
    display: flex;
    gap: 20px;
  }
  
  .job-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
  
  .job-card {
    background-color: #1f1f1f;
    border-radius: 8px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
  }
  
  .job-info {
    max-width: 70%;
  }
  
  .job-info h2 {
    color: white;
  }
  
  .job-info .posted-on, .job-info .price-range, .job-info .description, .job-info .skills {
    margin: 10px 0;
  }
  
  .job-info .view-details {
    background-color: #6a1b9a;
    color: #fff;
    padding: 8px 16px;
    border: none;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .client-info {
    text-align: right;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-left: 2px solid rgb(81, 81, 81);
    padding-left: 10px;
  }
  
  .client-image img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
  }
  
  .client-name {
    background: none;
    border: none;
    color: white;
    font-size: 1.2em;
  }
  .client-location {
    background: none;
    border: none;
    color: #6a1b9a;
    font-size: 14px;
  }
  .client-info p {
    margin-top: 10px;
    color: #ccc;
  }
  .view-profile {
    background-color: #6a1b9a;
    color: #fff;
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
  }