.header-section {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 80px;
    background-color: #1e1e1e; /* Dark background */
}

.header-content {
    max-width: 50%;
    color: #f1f1f1; /* Light text color */
}

.header-title {
    font-size: 3.5rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    color: #ffffff; /* White text color */
    margin-bottom: 20px;
}

.header-subtitle {
    font-size: 1.2rem;
    font-family: 'Poppins', sans-serif;
    color: #cccccc; /* Light gray text color */
    margin-bottom: 30px;
}

.header-search {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.search-input {
    width: 60%;
    padding: 12px 15px;
    font-size: 1rem;
    font-family: 'Poppins', sans-serif;
    border: 2px solid #444; /* Dark border */
    border-radius: 5px;
    margin-right: 10px;
    background-color: #333; /* Dark input background */
    color: #ffffff; /* Light text color */
}

.header-or {
    margin-right: 12px;
    color: #ffffff; /* Light text color */
}

.search-btn {
    padding: 15px 30px;
    font-size: 18px;
    background-color: #6a1b9a;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.search-btn:hover {
    background-color: #6a1b9a;
}

.header-tags {
    display: flex;
    flex-wrap: wrap;
}

.tag-button {
    background-color: #333; /* Dark button background */
    padding: 10px 15px;
    font-size: 12px;
    border: none;
    border-radius: 20px;
    margin-right: 5px;
    margin-bottom: 10px;
    cursor: pointer;
    color: #ffffff; /* Light text color */
}

.tag-button:hover {
    background-color: #444; /* Slightly lighter dark background */
}
