.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #1e1e1e; /* Dark background */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  border-bottom: 10px solid #000;
}

.navbar-logo {
  font-size: 26px;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
  color: white;
  text-decoration: none;
  transition: color 0.3s ease
}


.logo-link {
  color: white !important; /* Force the logo to be white */
  text-decoration: none;
  font-size: 24px;
  font-weight: bold;
  transition: color 0.3s ease;
}
.navbar-menu {
    list-style: none;
    display: flex;
    gap: 20px;
}

.navbar-menu li {
  margin: 0 15px;
}

.navbar-menu a {
  font-size: 1.2rem;
  color: #cccccc; /* Light gray text color */
  text-decoration: none;
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
}

.navbar-menu a:hover {
  color: #ffffff; /* White text color on hover */
}

.navbar-auth {
  display: flex;
  align-items: center;
}

.navbar-auth-btn {
  margin-right: 15px;
  font-size: 1.1rem;
  text-decoration: none;
  font-family: 'Poppins', sans-serif;
}

.sign-in {
  color: #007bff; /* Blue color */
}

.sign-up {
  color: #28a745; /* Green color */
}

.navbar-auth-btn:hover {
  color: #ffffff; /* White text color on hover */
}

.navbar-button {
  font-size: 1.1rem;
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-right: 15px;
}

.navbar-button:hover {
  background-color: #0056b3;
}
